import { Injectable, signal } from '@angular/core';

export type DialogType = 'info' | 'warning' | 'danger';

export interface ConfirmDialogConfig {
  type?: DialogType;
  title: string;
  message: string;
  confirmText?: string;
  cancelText?: string;
  affectedItems?: string[];
  requireConfirmText?: boolean;
  confirmTextRequired?: string;
}

export interface ConfirmDialogState extends ConfirmDialogConfig {
  isOpen: boolean;
  resolve?: (value: boolean) => void;
}

@Injectable({ providedIn: 'root' })
export class ConfirmDialogService {
  // å°è©±æ¡†ç‹€æ…‹
  state = signal<ConfirmDialogState>({
    isOpen: false,
    type: 'info',
    title: '',
    message: '',
    confirmText: 'ç¢ºå®š',
    cancelText: 'å–æ¶ˆ',
    affectedItems: [],
    requireConfirmText: false,
    confirmTextRequired: ''
  });

  /**
   * é¡¯ç¤ºç¢ºèªå°è©±æ¡†
   * @returns Promise<boolean> - ç”¨æˆ¶é»æ“Šç¢ºå®šè¿”å› trueï¼Œå–æ¶ˆè¿”å› false
   */
  confirm(config: ConfirmDialogConfig): Promise<boolean> {
    return new Promise((resolve) => {
      this.state.set({
        isOpen: true,
        type: config.type || 'info',
        title: config.title,
        message: config.message,
        confirmText: config.confirmText || 'ç¢ºå®š',
        cancelText: config.cancelText || 'å–æ¶ˆ',
        affectedItems: config.affectedItems || [],
        requireConfirmText: config.requireConfirmText || false,
        confirmTextRequired: config.confirmTextRequired || 'DELETE',
        resolve
      });
    });
  }

  /**
   * å±éšªæ“ä½œç¢ºèªï¼ˆç´…è‰²æ¨£å¼ï¼‰
   */
  danger(title: string, message: string, affectedItems?: string[]): Promise<boolean> {
    return this.confirm({
      type: 'danger',
      title,
      message,
      confirmText: 'åˆªé™¤',
      affectedItems
    });
  }

  /**
   * è­¦å‘Šç¢ºèªï¼ˆé»ƒè‰²æ¨£å¼ï¼‰
   */
  warning(title: string, message: string): Promise<boolean> {
    return this.confirm({
      type: 'warning',
      title,
      message,
      confirmText: 'ç¢ºå®š'
    });
  }

  /**
   * æ™®é€šç¢ºèªï¼ˆè—è‰²æ¨£å¼ï¼‰
   */
  info(title: string, message: string): Promise<boolean> {
    return this.confirm({
      type: 'info',
      title,
      message
    });
  }

  /**
   * è™•ç†ç¢ºå®š
   */
  onConfirm() {
    const currentState = this.state();
    if (currentState.resolve) {
      currentState.resolve(true);
    }
    this.close();
  }

  /**
   * è™•ç†å–æ¶ˆ
   */
  onCancel() {
    const currentState = this.state();
    if (currentState.resolve) {
      currentState.resolve(false);
    }
    this.close();
  }

  /**
   * é—œé–‰å°è©±æ¡†
   */
  private close() {
    this.state.update(s => ({ ...s, isOpen: false }));
  }
}
